.jp-jplayer{
    background: none;
}
.jp-audio, .jp-audio-stream, .jp-video, .jp-interface,.jp-details, .jp-playlist{
    background: none;
    width: 100%;
    border: none;
}
.jp-audio .jp-interface, .jp-audio-stream .jp-interface{
    height: auto;
}
.jp-play{
    position: absolute;
    left: 60px;
    top: 15px;
    &:hover{
        opacity: 0.8;
    }
}

.jp-details{
    position: absolute;
    left: 20%;
    top: 0;
    text-align: left;
    color: #fff;
    width: 80%;
}
.jp-details .jp-title{
        font-size: 17px;
    font-family: 'regular';
    font-weight: normal;
    line-height: 19px;
    padding: 15px 20px;
}
.jp-details .jp-title span{
font-size: 19px;
text-transform: uppercase;
 font-weight: normal;

}

.jp-volume-controls {
    position: absolute;
    top: 32px;
    right: 50px;
    width: 55px;
    z-index: 12;
}



